import React, {Component} from 'react';
// import {Link} from 'react-router-dom';
import {Fade, Reveal} from 'react-reveal/';
import Image from 'react-bootstrap/Image';
import '../assets/css/about.css';

class About extends Component {
    render(){
        let jhonData = this.props.jhonData; 
        var {aClass} = this.props;
        return(
            <section className={`${aClass}`} id="about">
                <div className="container" id="aboutCont">
                    <div className="row">
                        <div className="col-lg-7 d-flex align-items-center">
                            <Fade bottom cascade duration={1000}>
                                <div className="about_content">
                                    <h2 className="t_color">{jhonData.aboutme}</h2>
                                    <br></br>
                                    {/* <h6>{jhonData.role}</h6> */}
                                    {jhonData.aboutdetails}
                                    {/* <Link to="/"  className="theme_btn active">Hire Me</Link> */}
                                    {/* <Link to="/" className="theme_btn">Download CV</Link> */}
                                </div>
                            </Fade>
                        </div>
                        <div className="col-lg-5 img_cont">
                            <div className="about_img">
                                <Reveal effect="fadeInRight" duration={1500}>
                                <Image style={{maxHeight: 350}} roundedCircle src={require('../image/aboutMe.jpg')} alt="Susie Xia"/>
                                </Reveal>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}
export default About;
