import React, { Component } from "react";
import Sectiontitle from '../component/Banner/Sectiontitle';
import Image from 'react-bootstrap/Image';
import { Container, Row, Col } from "react-bootstrap";
import '../assets/css/contact.css'


class Contact extends Component {
  render() {
    return (
      <section className="contact-area" id="contact">
        <div className="container">
            <Sectiontitle Title="Get in Contact"/>
            <br></br>
              <Container fluid>
                <Row id="contactText">
                  <Col>
                    <Image fluid src={require('../image/collage2.png')} alt="collage"></Image>
                  </Col>
                  <Col>
                    <p>If you agree with my visions for CSESoc in 2025, then please help me achieve them by 
                    <b> voting for me as your #1 vote preference for Co-President and GEDI!</b></p>
                    <br></br>
                    <p>If you have any questions or want to chat to me about any new ideas or suggestions, 
                      send me a message on Messenger or Discord!</p>
                  </Col>
                </Row>
              </Container>
        </div>
      </section>
    );
  }
}
export default Contact;
