import React, {Component} from 'react';
import Sectiontitle from '../component/Banner/Sectiontitle';
import Fade from 'react-reveal/Fade';
import { Container } from 'react-bootstrap';
import '../assets/css/service.css'

class Service extends Component{
    render(){
        let jhonData = this.props.jhonData;
        var {wClass} = this.props
        return(
            <section className={`${wClass}`} id="service">
                <div className="container">
                    <Sectiontitle Title="Why should you vote for me?"></Sectiontitle> 
                    <p style={{"text-align":"center"}}>
                        As a director in CSESoc, it's made me very appreciative of how much this society has to offer and 
                        realise the potential that it has to improve and grow. I'd love the continue being part of CSESoc
                        in the exec team next year to continue building on the foundations set by previous teams and 
                        unlock more of CSESoc's potential. I hope to see CSESoc continuing to be welcome to all and providing
                        a diverse range of opportunities for everyone!
                    </p>
                    <br></br>
                    <Fade bottom cascade duration={1000}>
                        <div className="row" id='visions'>
                            {
                                jhonData.service && jhonData.service.map(item =>{
                                return(
                                        <div className="col-lg-5 col-sm-6" key={item.id}>
                                            <div className="work_item wow fadeInUp" data-wow-delay="0.1s">
                                                <i className={item.iconName}></i>
                                                <a href=".#"><h2 className="t_color">{item.serviceTitle}</h2></a>
                                                <Container fluid>{item.sDetails}</Container>
                                            </div>
                                        </div>
                                        )
                                    }
                                )
                            }
                        </div>
                    </Fade>
                </div>
            </section>
        )
    }
}

export default Service;