import React, {Component} from 'react';
import ItemGrid from './ItemGrid';
import Sectiontitle from '../../component/Banner/Sectiontitle';
import Image from 'react-bootstrap/Image';
import { Container, Col, Row } from 'react-bootstrap';
import '../../assets/css/portfolio.css';

class portfolio extends Component {
    render() {
      var {pClass}= this.props;
      return(
        <section className={`portfolio_area ${pClass}`} id="portfolio">
            <div className="container">
                <Sectiontitle Title="Contributions"/>
                <p>Over the past two years, I’ve had the privilege of being part of the Socials portfolio, serving as both a Subcommittee member and Director. Our mission has been to organise a variety of accessible events for the wider community. As a director, my co-directors and I managed the entire <b>peer mentoring program</b> at the beginning of the year, helping first-year students transition into UNSW through group activities and engaging events. We’ve also developed numerous events focused on <b>promoting physical and mental well-being</b>, as well as supporting <b>philanthropic causes</b>. I’ve truly enjoyed bringing these event ideas to life and addressing different dimensions of wellness.</p>
                  <br></br>
                <ItemGrid/>
                <br></br>
                <Container>
                  <Row id='otherCont'>
                  <Col>
                    <Image fluid src={require('../../image/collage1.png')}></Image>
                  </Col>
                  <Col>
                    <div>
                      <p style={{textAlign:"left"}}>
                        Besides my role as a director, I've been highly involved with CSESoc and CSE itself during my
                        two years of university. This includes:
                      </p>
                      <ul>
                        <li>2023 First Year Camp Campee</li>
                        <li>2023 Peer Mentee</li>
                        <li>2023 Socials Subcommittee</li>
                        <li>2023 All Years Camp Leader</li>
                        <li>2024 Yellow Shirts Volunteer</li>
                      </ul>
                    </div>
                  </Col>
                  </Row>
                </Container>
            </div>
        </section>
      )
    }
  }

  export default portfolio;
